@font-face {
    font-family: SpaceMono;
    src: url("/public/font/SpaceMono-Regular.ttf");
    
}
@font-face {
    font-family: WorkSans;
    src: url("/public/font/WorkSans-Regular.ttf");
  
}
*{
    color: white;
    font-family: WorkSans;
    padding: 0;
}
body{
    background-color: #2b2b2b;
/*  */
}
.h3-mm{
    font-size: 1.5rem;
    margin-top: -0.5rem;

}
header{
    width: auto;
    /* background-color: #A259FF; */
    height: 6rem;
    display: grid;
    justify-content: space-between;
    grid-template-columns: auto auto;
   
}
.i_header{
    color: #A259FF;
}
.p_left_header{
    margin-left: 1rem;
    cursor: pointer;
    font-size: 2rem;
}
.p_left_header:hover{
    color: #A259FF;
}

.left_header{
    padding: 1rem;
    width: 50vw;
    display: flex;
    flex-direction: row;
  }
  .right_header{
    padding: 1rem;
      width: 40vw;
      display: grid;
     
      grid-template-columns: auto auto auto auto;
  
  }
main{
   
    width: auto;
    height: auto;
    justify-content: center;
    display: grid;
    grid-template-columns: auto auto;
    align-items: center;
}
.div_left_side{
    width: 35vw;
    height: auto;
}
.div_right_side{
    top: 0;
    padding: 0;
    width: 35vw;
    border-radius: 20px;
    margin-left: 5px;
     
    height: auto;

}
.left_1{
    margin-top: 2rem;

    width: auto;
    height: auto;
    display: grid;
    grid-template-rows: auto auto auto;
    
}
.left_2{
    margin-top: 2rem;

    width: auto;
    height: auto;
    display: grid;
    grid-template-columns: auto auto auto;
    
}
.right_1{
    border-top:1px solid darkgray;
    border-left: 1px solid darkgray;
    border-right: 1px solid darkgray;
    padding-top: 1rem;
    width: 28vw;
    padding-left: 2vw;
    font-size: 1.5rem;
    height: 5vh;
    background-color: #3B3B3B;
    
}
.right_2{
    border-bottom:1px solid darkgray;
    border-left: 1px solid darkgray;
    border-right: 1px solid darkgray;
    padding-left: 2vw;
    width: 28vw;
    height: auto;
    font-size: 1.5rem;
    display: flex;
    border-end-end-radius: 20px;
    border-bottom-left-radius: 20px;
    flex-direction: row;
    background-color: #3B3B3B;


}
.img_main{
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
    width: 30vw;
    height: 20rem;
}
.img_prf_right{
    width: 50px;
    height: 50px;
    border-radius: 50%;
}

.p_prof{
    margin-left: 8px;
    font-size: 1rem;

}
.btn_start{
    margin-top: 2rem;
    font-size: 1.5vw;
  color: white;
    width: 70%;
    background-color: #A259FF;
    text-align: center;
    border-radius: 15px;
    border: 0px;
    cursor: pointer;

    height: 9vh;
}
.btn_right_header{
    width: 8vw;
    font-size: 1vw;
    background-color: #A259FF;
    text-align: center;
    border-radius: 15px;
    border: 0px;
    height: 2rem;
    cursor: pointer;

}
#i_hamerger{
    display: none;
}

.mobile_underProfile_img{
   display: none;
}
.btn_start_mobile{
    display: none;
}
#p_h_1,#p_h_2,#p_h_3{
    font-size: 1.3vw;
    cursor: pointer;
}
#p_h_2:hover,#p_h_3:hover,#p_h_1:hover{
    color: #A259FF;
}

h1{
    font-size: 4vw;
}
#down_p_main{
    font-size: 1.5vw;
}
#p_main_down{
    font-size: 1.5vw;
}
#p_main_down_1{
    color: gray;
}
h3{
    font-size:1rem;
}
#ranking_footer:hover{
    color: #A259FF;
    cursor: pointer;
}
@media only screen and (max-width: 800px) {
  
    #i_hamerger{
        display: block;
    }
    #i_hamerger:hover{
    
        color: #A259FF;
        cursor: pointer;
    }
    #p_h_1,#p_h_2,#p_h_3,.btn_right_header{
        display: none;
    }
main{
    right: 0;
    left: 0;
    margin: auto;
    display: flex;
    flex-direction: column;
    margin-top: 8rem;
    width: auto;
    justify-content: left;
    align-items: flex-start;

}
.left_header{
    width: auto;
}
header{
    width: 90vw;
}

.right_header{
    width: auto;
    justify-content: flex-end;
}
.img_prf_right{
    width: 40px;
    height: 40px;
}
#down_p_main{
    font-size: 4vw;
}
  .btn_start{
    display: none;
  }
    h1{
        font-size: 2rem;
    }
    .btn_start{
        font-size: 2vw;

    }
    .left_1{
        margin-top: -7rem;
        width: 85vw;
        position: relative;
        margin-left: 0;
        padding-left:1.5rem;
    }
    .left_2{
        height: auto;
         
        width: 60vw;
         display: none;
        margin-top: 6rem;
         font-size:15px;
    }
    .img_main{
        margin-top: 1rem;
        border-top-left-radius:35px ;
        width: 22rem;
        height: 12rem;
        padding-left:1rem;

    }
    .div_left_side{
        height: auto;
    }
    .div_right_side{
        height: 20rem;
        display: flex;
        flex-direction: column;
    }
    .right_1{
        width: 20rem;
        padding-left: 2rem;
        padding-top: 2rem;
        margin-left:1rem;
        height: 3rem;
        
    }
    .right_2{
        width: 20rem;
        height: 3rem;
        padding-left: 2rem;
        margin-left:1rem;
    }
    
    .mobile_underProfile_img{
        display: grid;
        grid-template-rows:auto auto ;
        width: 84vw;
        height: 3rem;
        padding-left: 2rem;
        padding-top: 2vh;
        margin-left:1.3rem;
        
    }
    .mobile_underProfile_img_up{
        width: 100%;
        height: 5rem;
    }
    .mobile_underProfile_img_down{
        width: 100%;
        height: 5rem;
        display: grid;
        grid-template-columns: auto auto auto;
    }
    .btn_start_mobile{
        font-family: WorkSans;

        display: block;
        width: 90%;
        height: 3.5rem;
        border-radius: 25px;
        text-align: center;
        border: 0px;
        background-color: #A259FF;
        color: aliceblue;
        
    }
    .under_p{
        color: gray;
        font-size: 13px;
        font-weight: bold;
    }
    .p_left_header{
        font-size: 1.5rem;
    }
  
   
  }