footer{
    font-size: 1vw;
    background-color: #3B3B3B;
    margin-top: 5rem;
    display: grid;
    width: 100%;
    grid-template-columns: 20% 20% 20%;
    align-items: center;
    row-gap: 1rem;
    justify-content: center;
    height: 15rem;
}
.footer_first_cl{
    display: flex;
    align-items: center;
    padding-top: 1rem;
    flex-direction: row;
}
.div_wallet_footer, .footer_socail_media{
    display: flex;
     flex-direction: column;
    gap:1rem ;
}
.footer_socail_media_part2{
    display: flex;
    flex-direction: row;
    gap: 1rem;
}
.div_subs_footer{
    display: flex;
    gap: 0.5rem;
    flex-direction: row;
}
.div_subs_footer > button{
    width: 12vw;
    cursor: pointer;
    color: white;

    gap: 1rem;
    border-radius: 20px;
    background-color: #A259FF;
    border: 0;
    align-items: center;
    height: 3rem;
    text-align: center;
    display: flex;
    justify-content: center;
    
}
#input_email{
    outline: #A259FF;
}
.div_subs_footer > input{
    width: 12vw;
    gap: 1rem;
    border-radius: 20px;
    border: 0;
    align-items: center;
    height: 3rem;
    color: black;
    text-align: center;
    display: flex;
    justify-content: center;
}
.div_between_footer{
    width: 60vw;height: 1px;background-color: aliceblue;
}
.p_lp{
    font-size: 1rem;
}
.created_by_footer{
    color: gray;
    font-size: small;
}
#instagram:hover ,#github:hover ,#linkedin:hover,#telegram:hover{
    cursor: pointer;
    color: #A259FF;
}

#ranking_footer:hover,#wallet_footer:hover,#market_footer:hover{
    color: #A259FF;
    cursor: pointer;
}

@media    (max-width: 800px){
    footer{
        display: flex;
        flex-direction: column;
        height: auto;
        align-items: center;
        gap: 2rem;
        justify-content: center;
        font-size: 3.5vw;
    }
    .div_wallet_footer{
        align-items: center;
    }
    .footer_socail_media_part2{
        align-items: center;
        justify-content: center;
    }
    .div_subs_footer > button{
        width: 40vw;
    }
    .div_subs_footer > input{
        width: 40vw;
    }
    .div_subs_footer{
        flex-direction: column;
    }

}
@media    (800px <= width <= 1300px){
    footer{
        font-size: 1.4vw;
    }
    .div_subs_footer{
        flex-direction: column;
    }
    .div_subs_footer > button{
        width: 15vw;
        font-size: 1.5vw;
    }
    .div_subs_footer > input{
        width: 15vw;
        font-size: 1.2vw;
    }        

   
}