
.trending{
    margin-top: 10rem;
    width: 65vw;
    margin-left: 14%;
    /* auto */
    height: auto;
    display: grid;
    grid-template-rows: 25% 75% ;

}

.up_div_trending{

    display: grid;
    grid-template-rows: auto auto;
}
.h3_T{
    margin-left: 1rem;

    font-size: 2vw;
}
.p_up_t{
    margin-left: 1rem;
    margin-top: 1rem;
    font-size: 1.5vw;
    color: gray;
}
.down_div_trending{
    width: auto;
    margin-top: 1rem;
    padding-left: 1rem;
    display: grid;
    height: auto;
    grid-template-columns: auto auto auto;
}
.img_T_1{
    margin-left: 1rem;

    height:20rem;
    width:20rem;
    border-radius: 15px;
}
.img_T_2{
    height:20rem;
    margin-left: 1rem;
    width:20rem;
    border-radius: 15px;
}
.img_T_3{
    height:20rem;
    width:20rem;
    margin-left: 1rem;

    border-radius: 15px;
}
.div_T_prof{
    margin-left: 1rem;

    display: grid;
     grid-template-columns: auto auto auto;
      width: 20rem;
      margin-top: 1rem;
      gap: 1rem;
}
.img_T_prof_1{

    height: 6rem;
    width: 6rem;
    border-radius: 15px;
}

.img_T_prof_2{
    
    height: 6rem;
    width: 6rem;
    border-radius: 15px;
}
.img_T_prof_3{
    display: flex;
    height: 6rem;
    width: 6rem;
    border-radius: 15px;
    background-color: #A259FF;
    align-items: center;
    justify-content: center;
    font-size: 1.3;
    
}

.h3_under_T{
    margin-top: 0.5rem;
    font-size: 1.2vw;
    margin-left: 1rem;

}
.div_under_prof_T{
    margin-top: 0.5rem;
    width: 7rem;
    display: grid;
    margin-left: 1rem;

    grid-template-columns: auto auto;
}
.img_T_under{
    
    width: 2.5rem;
    height: 2.5rem;
    border-radius: 50%;
}
.name_prof_p_T{
    font-size: 1rem;
    margin-top: 0.5rem;
}

@media only screen and (max-width: 800px) {
    .down_div_trending{
        /* margin-left: 10vw; */
        grid-template-columns: auto;
    }
     #div_T_prof_2,#div_T_prof_3, #div_under_prof_T_3,#div_under_prof_T_2,#h3_under_T_3,#h3_under_T_2,.img_T_3,.img_T_2{
        display: none;
    }
    .h3_T{
        font-size: 5vw;
        margin-left: 1rem;

    }
    .h3_under_T{
        font-size: 1.1rem;
    }
    .name_prof_p_T{
        font-size: 1rem;
    }
    .p_up_t{
        font-size:3.5vw;
    }
    .trending{
        margin-left:5vw; 
    }
    .img_T_1{
        margin-top: 1rem;
        width: 70vw;
    }
    .div_T_prof{
        width: 75vw;
    }
    .img_T_prof_1,.img_T_prof_2 ,.img_T_prof_3{
        width: 5rem;
    }
    .img_T_prof_3 > p{
        font-size: 1rem;
    }
}
@media    (800px <= width <= 1300px){
    .trending{
        margin-left: 9%;
    }
    .down_div_trending {
        margin-left: 0;
        grid-template-columns: auto auto;
    }


    #div_T_prof_3, #div_under_prof_T_3,#h3_under_T_3,.img_T_3{
        display: none;
    }
     .h3_T{
        font-size: 3vw;
    }
    .h3_under_T{
        font-size: 1.5vw;
    }
    .p_up_t{
        font-size:1.9vw;
    }
    
}
