

.topcreators{
     /* background-color: #A259FF; */
     margin-top: 5rem;
      width: 62vw;
      margin-left: 15vw;
     height: auto;
}
.top_tc{
    width: 100%;
    height: 30%;
    display: flex;
    flex-direction: column;
    /* background-color: cadetblue; */
}
.down_tc{
    width: 100%;
    height: auto;
    margin-top: 2em;
}
#h3_tc{
    font-size: 2vw;
}
.top_tc_part_2{
    margin-top: 2.2vh;
    font-size: 1.2vw;
    color: gray;
    display:  grid;
    grid-template-columns: auto auto;
    justify-content: space-between;

}
.btn_tc_top{
    background-color: #2b2b2b;
    border: 3px solid #A259FF;
    width: 17vw;
    height: 8vh;
    font-size: 1.3vw;
    border-radius: 20px;
    color: white;
    cursor: pointer;
}
.btn_tc_top:hover{
    background-color: #A259FF;
    border: 3px solid #2b2b2b;
    color: #2b2b2b;
}
#p_top_tc{
    margin-top: 2vh;
    color: gray;
}
.down_tc{
    display: grid;
    grid-template-columns: auto auto auto auto;
    justify-content: space-between;
}
.prof_tc_div{
    margin-top: 1rem;
    background-color: #3B3B3B;
    width: 15vw;
    height: 14rem;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    align-items: center;

}
.img_prof_tc{
    margin-top: 1vh;
    border: 2px solid #A259FF;
    width: 7rem;
    height: 7rem;
    border-radius: 50%;
}
#name_prof_tc{
    font-size: 1vw;
    margin-top: 0.5vh;
}
.under_top_tc_profile{
    margin-top: 1.4vh;
    display:  grid;
    text-align: center;
    grid-template-rows:  auto ;
    justify-content: center;
    
}
#u_t_c_1{
    display: grid;
}
#u_t_c_1{
    display: grid;
    
}
#total_tc_p{
    color: gray;
}
@media only screen and (max-width: 800px) {
.topcreators{
    margin-top: 10rem;
}
#h3_tc{
    font-size: 5vw;
}
#p_top_tc{
    font-size: 3vw;
}
.btn_tc_top{
    font-size: 2.2vw;
}
.down_tc{
    display: flex;
    flex-direction: column;
    height: auto;
}
.down_tc > div:nth-child(n+5){
    display: none;
    
}


.prof_tc_div{
    width: 65vw;
    height: 5rem;
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    
}
.img_prof_tc{
    margin-top: 0;
    width: 4rem;
    margin-left: 1em;
    height: 4rem;
}


#name_prof_tc{
    margin-left: 2em;
    font-size: 4vw;
}
#u_t_c_2{
        margin-left: 2em;

    font-size: 3vw;

}
.btn_tc_top{
    display: none;
}
.top_tc{height: auto;}

}
@media    (800px <= width <= 1300px){
    
    #h3_tc{
        font-size: 4vw;
    }
    #p_top_tc{
        font-size: 1.8vw;
    }
    .btn_tc_top{
        height: 2.2rem;
        font-size: 2.2vw;
    }
    .down_tc{
        display: grid;
        grid-template-columns: auto auto;
        gap: 1rem;
        height: auto;
    }
    .prof_tc_div{
        width: 20rem;
        height: 5rem;
        display: flex;
        flex-direction: row;
        
    }
    .img_prof_tc{
        margin-top: 0;
        width: 4rem;
        margin-left: 1em;
        height: 4rem;
    }
    .down_tc > div:nth-child(n+7){
        display: none;
        
    }
    #name_prof_tc{
        font-size: 1.8vw;
    }
}