

.top_main{
    width:60%;
    justify-content: center;
    flex-direction: column;
    gap: 2rem;
    height: 27rem;
    display: flex;
    right: 0;
    top: 0;
    margin: auto;
}
.div_search{
    border: 2px solid rgb(115, 115, 115);
    background-color: #2b2b2b;
    border-radius: 205px;
    width: 100%;
    display: flex;
    flex-direction: row;
    height: 4.5rem;

}
h1{
    font-size: 3vw;
}
h3{
    font-size:1.5rem;
}
.input_search{
    color: white;
    text-align: left;
    color: white;
    font-size: 1.5rem;
    padding-left: 2rem;
    outline: none;
    background-color: #2b2b2b;
    width: 90%;
    border-radius: 205px;
    height: 100%;
    border: 0px;
}
.input_search::placeholder{
   color: rgb(115, 115, 115);
}
.div_icon_main_top{
    width: 8%;
    height: 100%;
    align-items: center;
    display: flex;
    justify-content: flex-end;
}
.i_search{
  color: white;
  cursor: pointer;
}
.i_search:hover{
  color: #A259FF;
}
.div_swap{
    border-top: 1px solid rgb(115, 115, 115);
    width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: row;
    height: 5rem;

}
.nft_left ,.collect_right{
    cursor: pointer;
    width: 25%;
    border-bottom: 2px solid white;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}
.nft_left:hover > p ,.collect_right:hover > p{
    color: #A259FF;
}
.circle_nft,.circle_collect{
    display: flex;
    width: 3vw;
    height: 2vw;
    background-color: #3B3B3B;
    border-radius:30px;
    text-align: center;
    color: white;
    font-size: 1vw;
    align-items: center;
    justify-content: center;
    
}

.p_nft,.p_collect{
    font-size: 1.5rem;
}
.nft_show_card{
    display: grid;
    /* display: flex;
    flex-direction: column; */
    width: 100%;
    height: 100%;
    justify-content: space-evenly;
    background-color: #3B3B3B;
    gap: 1rem;
    /* flex-direction: row; */
    grid-template-columns: auto auto auto;
}
.card_DIC{
    margin-top: 2rem;
    height:32rem;
    width:25rem;
    display: flex;
    flex-direction: column;
    
    border-radius: 15px;
    background-color: #2b2b2b;
}
.img_card_DIC{
    width: 100%;
    height: 22rem;
    border-top-left-radius:15px ;
    border-top-right-radius: 15px;
}
.card_DIC_down{
    padding: 1rem;
   height: 10rem;
    width: 100%;
    display: flex;
    flex-direction: column;
}
.img_DIC_under{
   
    width: 1.6rem;
    border-radius: 50%;
    height: 1.6rem;
}
.card_DIC_down_part_one{
    margin-top: 0.5rem;
    display: flex;
    align-items: center;
    flex-direction: row;
}
.card_DIC_down_part_one > p{
    margin-left: 0.2rem;
}
.card_DIC_down_part_two{
    margin-top: 1rem;
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-around;
}
.dic_1_under{
    display: grid;
    grid-template-rows: auto auto;
}
.gray_DIC{
    color: gray;
    font-size: 0.8rem;
}
.p_DIC_galaxy{
    font-size: 1.2vw;
}
@media only screen and (max-width: 800px) {
  
   
.img_prf_right{
    width: 40px;
    height: 40px;
}
#down_p_main{
    font-size: 4vw;
}
  .btn_start{
    display: none;
  }
    h1{
        font-size: 5.5vw;
    }
    .btn_start{
        font-size: 2vw;

    }
    .left_1{
        margin-top: -7rem;
        width: 85vw;
        position: relative;
        margin-left: 0;
        padding-left:1.5rem;
    }
    .left_2{
        height: auto;
         
        width: 60vw;
         display: none;
        margin-top: 6rem;
         font-size:15px;
    }
    .img_main{
        margin-top: 1rem;
        border-top-left-radius:35px ;
        width: 85vw;
        height: 35vh;
        padding-left:1rem;

    }
    .right_1{
        width: 79.5vw;
        padding-left: 2rem;
        padding-top: 2rem;
        margin-left:1rem;
        height: 3rem;
    }
    .div_left_side{
        height: auto;
    }
    .div_right_side{
        height: auto;
    }
    .right_2{
        width: 79.5vw;
        height: 3rem;

        padding-left: 2rem;
        margin-left:1rem;
    }
    
    .mobile_underProfile_img{
        display: grid;
        grid-template-rows:auto auto ;
        width: 84vw;
        height: 3rem;
        padding-left: 2rem;
        padding-top: 2vh;
        margin-left:1.3rem;
        
    }
    .mobile_underProfile_img_up{
        width: 100%;
        height: 5rem;
    }
    .mobile_underProfile_img_down{
        width: 100%;
        height: 5rem;
        display: grid;
        grid-template-columns: auto auto auto;
    }
    .btn_start_mobile{
        font-family: WorkSans;

        display: block;
        width: 90%;
        height: 3.5rem;
        border-radius: 25px;
        text-align: center;
        border: 0px;
        background-color: #A259FF;
        color: aliceblue;
        
    }
    .under_p{
        color: gray;
        font-size: 13px;
        font-weight: bold;
    }
    .p_left_header{
        font-size: 1.5rem;
    }
    h3{
        font-size: 1rem;
    }
    .input_search::placeholder{
        font-size: 3vw;
     }
     .p_nft,.p_collect{
        font-size: 1rem;
    }
    .circle_nft,.circle_collect{
        display: none;
    }
    .nft_show_card{
        grid-template-columns: auto;
    }
    .nft_show_card >div:nth-child(n+7){
        display: none;
    }
  }
  @media    (800px <= width <= 1300px){
    .nft_show_card{
        grid-template-columns: auto auto;
    }
    .nft_show_card >div:nth-child(n+9){
        display: none;
    }
    h3{
        font-size: 1rem;
    }
    .circle_nft,.circle_collect{
        width: 4vw;
        height: 3vw;
        font-size: 2vw;
    }
   
}