
*{
    color: white;
    margin: 0;
    font-family: WorkSans;

    padding: 0;
}
body{
    background-color: #2b2b2b;
/*  */
}
.top_main{
    width:60%;
    justify-content: center;
    column-gap: 2rem;
    gap: 2rem;
    height: auto;
    right: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    margin: auto;
}

h1{
    font-size: 3vw;
}
h3{
    margin-top: 1rem;
    font-size:1.5rem;
}

.div_navigation_main{
    margin-top: 1rem;

    display: grid;
    gap: 1rem;
    grid-template-columns: auto auto auto auto;
    padding-bottom: 1rem;
}
.div_navigation_main > div{
    font-size: 1.5rem;
    cursor: pointer;
    text-align: center;
    border-bottom: 2px solid rgb(115, 115, 115);

}
.div_navigation_main > div:nth-child(1){
    
    border-bottom: 2px solid #A259FF;


}
.div_navigation_main > div:hover{
    color: #A259FF;
}
.bilboard{
        margin-top: 1rem;

    align-items: center;
    justify-content: space-evenly;
    border-radius:15px;
    display: grid;
    grid-template-columns: 4% 30% 20% 20% 20%;
    width: 100%;
    height: 2.5rem;
    border: 1px solid rgb(115, 115, 115);
}
.bilboard-iteam{
        margin-top: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    
}

.bilboard-iteam > div{
    width: 100%;
    
    align-items: center;
    justify-content: space-evenly;
    height: 4.5rem;
    border-radius: 15px;
    background-color: #3B3B3B;
    display: grid;
    grid-template-columns: auto auto 30% 20% 20% 20.5%;
}
.bilboard-iteam >div> p:nth-child(4){
    color: green;
}
.bilboard-iteam >div> p:nth-child(3){
    font-size: 1.3rem;
    margin-left: 1rem;
}
.bilboard-iteam >div> p:nth-child(3):hover{
    font-size: 1.3rem;
    margin-left: 1rem;
}
.numbersDiv{
    border-radius: 50%;
    background-color: #2b2b2b;
    text-align: center;
    align-items: center;
    width: 1.5rem;
    font-size: 1.1rem;
    height: 1.5rem;
}
.img_artist{
    width: 3.2rem;
    height: 3.2rem;
    cursor: pointer;
   
    border-radius: 50%;
}
.img_artist:hover{
    transform: scale(0.90);
}

@media only screen and (max-width: 800px) {
   .top_main{
    width: 80%;
   }
    h1{
        font-size: 1.5rem;
    }
    h3{
        font-size: 1rem;
    }
  .div_navigation_main > div{
    font-size: 2.6vw
  }
  .bilboard{
    
    margin-top: 0;
    grid-template-columns:1% 45%  auto;
  }
  .bilboard >p:nth-child(n+3 ){
    display: none;
  }
  .bilboard >p:nth-child(5){
    display: block;
  }
  .bilboard-iteam > div{
    width:100%;
    justify-content: space-around;
    font-size: 1rem;
    grid-template-columns:1% 3% 30%  auto ;
  }
  #p_ain{
    font-size:4vw;
  }
  .bilboard-iteam >div >p:nth-child(4),.bilboard-iteam >div >p:nth-child(5){
    display: none;
    
  }
}