.baner{
    display: flex;
    top: 0;
    left: 0;
    right: 0;
    margin-top: 2rem;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
    width: 100%;
    height: 40rem;
    background-image: url(../../../public/image/baner.jpg);
    background-repeat: no-repeat;
  background-size: auto;
  background-size: 100% 50rem;
}

.div_inside_baner{
    width: 100%;
    display: grid;
    grid-template-columns: auto auto;
}
.baner_prof{
    background-color: #3B3B3B;
    border-radius: 15px;
    width: 9rem;
    align-items: center;
    padding: 0.3rem;
    height: 2rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    cursor: pointer;
}

.baner_prof > p{
    margin-left: 0.2rem;
    
}
.img_baner_prof{
    border-radius: 50%;
    width:1.8rem;
    height:1.8rem;
}

.left_baner {
display: flex;
flex-direction: column;
gap: 1rem;
justify-content: space-between;
}
.btn_baner{
    cursor: pointer;
    align-items: center;
    justify-content: center;
    margin-top: 1rem;
    display: flex;
    flex-direction: row;
    background-color: #A259FF;
    border-radius: 20px;
    border: 2px solid white;
    color: black;
    text-align: center;
    font-size: 1rem;
    font-weight: bold;
    width: 12rem;
    height: 4rem;
}
.div_timer_baner{
    background-color: #3B3B3B;
    width: 25vw;
    border-radius: 15px;
    opacity: 0.8;
    height: 8rem;
    display: grid;
    grid-template-rows: auto auto;
    align-items: center;
    justify-content: center;
    padding: 1.1rem;
    border: 1px solid #A259FF;
}
.div_timer_baner > p{
    font-size: 1.5rem;

}
.div_timer_baner > h2{
     
    font-size: 2.3rem;
}
@media    (max-width: 800px){
    .div_inside_baner{
        display: flex;
        gap: 1.4rem;
        flex-direction: column;
        align-items: center;
    }
    .div_timer_baner{
        width: 60vw;
    }
    
}
@media    (800px <= width <= 1300px){
    .baner_prof{
      margin-left: 2rem;
    }
    .left_baner{
        padding: 2rem;
    }
    .div_timer_baner > p{
        font-size: 1rem;
    
    }
    .div_timer_baner > h2{
         
        font-size: 1.6rem;
    }
    
   
}