
*{
    color: white;
    margin: 0;
    font-family: WorkSans;

    padding: 0;
}
body{
    background-color: #2b2b2b;
/*  */
}
.main_prof{
    display: flex;
    flex-direction: column;
}
.img_background{
    width: 100%;
    height: 24rem;
    margin: auto;
    display: flex;

}
.main_div{
    display: grid;
    grid-template-columns: auto auto;
    top: 0;
    right: 0;
    margin: auto;
    width: 55%;
    height: auto;

}
.div_right{
    list-style: none;
    display: flex;
    flex-direction: column;
    height: auto;
}
.div_left{
    list-style: none;
    display: flex;
    flex-direction: column;
    height: auto;
    row-gap: 2rem;
}
.top_img{
    display: flex;
    flex-direction: column;
    row-gap: 1rem;
}
.img_prof{
    border-radius: 15px;
    width: 8rem;
    height: 8rem;
    margin-top: -5rem;
}
h3{
    font-size: 3rem;
}
.middle_list{
    font-size: 2rem;
    display: grid;
    grid-template-columns: auto auto auto;
}
.middle_list > p:nth-child(n+4){
    font-size: 1.6rem;
    color: rgb(115, 115, 115);
}
.bio_list{
    display: flex;
    font-size: 1.7rem;

    flex-direction: column;

}
.bio_list >p:nth-child(1){
    color:  rgb(115, 115, 115);
}
.down_list {
    display: flex;
    gap: 1rem;
     flex-direction: column;
}
.down_list > div{
    display: flex;
    flex-direction: row;
    column-gap: 1.2rem;
}
.down_list > p{
    font-size: 1.7rem;
    color: rgb(115, 115, 115);
}
.down_list > div > i{
    color: rgb(115, 115, 115);
    cursor: pointer;

}
.down_list >div > i:hover{
    color: #A259FF;

}
.right_li{
    display: flex;
    flex-direction: row;
    gap: 1rem;
    padding: 6rem;
}
.btn_follow{
    background-color: #2b2b2b;
    border-radius: 20px;
    text-align: center;
    align-items: center;
    justify-content: space-evenly;
    display: flex;
    flex-direction: row;
    color: white;
    border: 2px solid #A259FF;
    font-size: 1.3rem;
    width: 10rem;
    height: 4rem;
    cursor: pointer;
}
.btn_direct{
    background-color: #A259FF;
    color: white;
    border-radius: 20px;
    text-align: center;
    align-items: center;
    justify-content: space-evenly;
    display: flex;
    flex-direction: row;
    font-size: 1.3rem;
    border: 0px;
    width: 10rem;
    height: 4rem;
    cursor: pointer;
}
.btn_follow > i{
    color: #A259FF;
}

/* swap */
.div_swap{
    border-top: 1px solid rgb(115, 115, 115);
    width: 100%;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: row;
    height: 5rem;

}
.nft_left ,.collect_right{
    cursor: pointer;
    width: 25%;
    border-bottom: 2px solid white;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 1rem;
}
.nft_left:hover > p ,.collect_right:hover > p{
    color: #A259FF;
}
.circle_nft,.circle_collect{
    display: flex;
    width: 3vw;
    height: 2vw;
    background-color: #3B3B3B;
    border-radius:30px;
    text-align: center;
    color: white;
    font-size: 1vw;
    align-items: center;
    justify-content: center;
    
}
.p_nft,.p_collect{
    font-size: 1.5rem;
}
.nft_show_card{
    display: grid;
    width: 100%;
    height: auto;
    justify-content: center;
    background-color: #3B3B3B;
    gap: 1rem;
    grid-template-columns: auto auto auto;
}
.card_DIC{
    margin-top: 2rem;
    height:32rem;
    width:25rem;
    display: flex;
    flex-direction: column;
    border-radius: 15px;
    background-color: #2b2b2b;
}
.img_card_DIC{
    width: 100%;
    height: 22rem;
    border-top-left-radius:15px ;
    border-top-right-radius: 15px;
}
.card_DIC_down{
    padding: 1rem;
   height: 10rem;
    width: 100%;
    display: flex;
    flex-direction: column;
}
.img_DIC_under{
   
    width: 1.6rem;
    border-radius: 50%;
    height: 1.6rem;
}
#img_back_bg{
    width: 100%;
}
.card_DIC_down_part_one{
    margin-top: 0.5rem;
    display: flex;
    align-items: center;
    flex-direction: row;
}
.card_DIC_down_part_one > p{
    margin-left: 0.2rem;
}
.card_DIC_down_part_two{
    margin-top: 1rem;
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-around;
}
.dic_1_under{
    display: grid;
    grid-template-rows: auto auto;
}
.gray_DIC{
    color: gray;
    font-size: 0.8rem;
}
.p_DIC_galaxy{
    font-size: 1.2vw;
}
@media only screen and (max-width: 800px) {


.img_background{
    width: 100%;
    height: 15rem;
}
.main_div{
    display: flex;
    flex-direction: column;
}
.div_left > li:nth-child(1){
    align-items: center;
}
.div_left > li:nth-child(4){
    font-size: 4vw;
}
.down_list >p{
    font-size: 4vw;
}
h3{
    font-size: 6vw;
}
.middle_list{
    font-size: 4vw;
}
.middle_list > p:nth-child(n+4){
    font-size: 3vw;
    color: rgb(115, 115, 115);
}
.right_li{
    display: flex;
    padding: 0;
    flex-direction: column;
    align-items: center;
    height: auto;
    margin-top: 2rem;
}
.right_li > button{
      width: 20rem;
      justify-content: center;
      gap: 1rem;
}
/* swap */
.left_1{
    margin-top: -7rem;
    width: 85vw;
    position: relative;
    margin-left: 0;
    padding-left:1.5rem;
}
.left_2{
    height: auto;
     
    width: 60vw;
     display: none;
    margin-top: 6rem;
     font-size:15px;
}
.img_main{
    margin-top: 1rem;
    border-top-left-radius:35px ;
    width: 85vw;
    height: 35vh;
    padding-left:1rem;

}
.right_1{
    width: 79.5vw;
    padding-left: 2rem;
    padding-top: 2rem;
    margin-left:1rem;
    height: 3rem;
}
.div_left_side{
    height: auto;
}
.div_right_side{
    height: auto;
}
.right_2{
    width: 79.5vw;
    height: 3rem;

    padding-left: 2rem;
    margin-left:1rem;
}

.mobile_underProfile_img{
    display: grid;
    grid-template-rows:auto auto ;
    width: 84vw;
    height: 3rem;
    padding-left: 2rem;
    padding-top: 2vh;
    margin-left:1.3rem;
    
}
.mobile_underProfile_img_up{
    width: 100%;
    height: 5rem;
}
.mobile_underProfile_img_down{
    width: 100%;
    height: 5rem;
    display: grid;
    grid-template-columns: auto auto auto;
}
.btn_start_mobile{
    font-family: WorkSans;

    display: block;
    width: 90%;
    height: 3.5rem;
    border-radius: 25px;
    text-align: center;
    border: 0px;
    background-color: #A259FF;
    color: aliceblue;
    
}
.under_p{
    color: gray;
    font-size: 13px;
    font-weight: bold;
}
.p_left_header{
    font-size: 1.5rem;
}
h3{
    font-size: 1rem;
}
.input_search::placeholder{
    font-size: 3vw;
 }
 .p_nft,.p_collect{
    font-size: 1rem;
}
.circle_nft,.circle_collect{
    display: none;
}
.nft_show_card{
    grid-template-columns: auto;
}
.nft_show_card >div:nth-child(n+7){
    display: none;
}
}
@media    (800px <= width <= 1300px){

    .main_div{
        display: flex;
        flex-direction: column;
        margin: 0;
        padding-left: 5rem;
    }
    .div_left > li{
        align-items: flex-start;
    }
    h3{
        font-size: 4vw;
    }
    .middle_list{
        font-size: 3vw;
    }
    .right_li{
        display: flex;
        padding: 0;
        flex-direction: row;
        align-items: flex-start;
        height: auto;
    }
    .right_li > button{
      width: 15rem;
      justify-content: center;
      gap: 1rem;
}
/* swap */
.nft_show_card{
    grid-template-columns: auto auto;
}
.nft_show_card >div:nth-child(n+9){
    display: none;
}
h3{
    font-size: 1rem;
}
.circle_nft,.circle_collect{
    width: 4vw;
    height: 3vw;
    font-size: 2vw;
}
    
}