
.discoverme{
    display: grid;
    grid-template-rows: auto auto;
    margin-top: 5rem;
    width: 62vw;
    height: auto;
    margin-left: 15vw;
    
}
.p_num{
    font-size: 1rem;
}
.name_p{
    font-size: 1rem;
}
.h3_up_DIC{
    font-size: 2vw;
}
.div_up_DIC_part_2{
    align-items: center;
    justify-content: space-between;
    display: grid;
    grid-template-columns: auto auto;

}
.div_up_DIC_part_2 > p{
    font-size: 1.1vw;
}
.btn_DIC_top{
    background-color: #2b2b2b;
    border: 3px solid #A259FF;
    width: 17vw;
    height: 8vh;
    font-size: 1.3vw;
    border-radius: 20px;
    color: white;
    cursor: pointer;
}
.btn_DIC_top:hover{
    background-color: #A259FF;
    border: 3px solid #2b2b2b;
    color: #2b2b2b;
}
.div_down_DIC{
    display: grid;
    margin-top: 0.5rem;
    width: 62vw;
    gap: 1rem;
    grid-template-columns: auto auto auto;
}
.card_DIC{
    margin-top: 2rem;
    height:auto;
    width:1rem;
    display: grid;
    grid-template-rows: 13rem 40%;
    border-radius: 15px;
    background-color: #3B3B3B;
}
.img_card_DIC{
    width: 1px;
    height: 13rem;
    border-top-left-radius:15px ;
    border-top-right-radius: 15px;
}
.card_DIC_down{
    padding: 1rem;

    width: 100%;
    display: flex;
    flex-direction: column;
}
.img_DIC_under{
   
    width: 1.6rem;
    border-radius: 50%;
    height: 1.6rem;
}
.card_DIC_down_part_one{
    margin-top: 0.5rem;
    display: flex;
    align-items: center;
    flex-direction: row;
}
.card_DIC_down_part_one > p{
    margin-left: 0.2rem;
}
.card_DIC_down_part_two{
    margin-top: 1rem;
    display: grid;
    grid-template-columns: auto auto;
    justify-content: space-around;
}
.dic_1_under{
    display: grid;
    grid-template-rows: auto auto;
}
.gray_DIC{
    color: gray;
    font-size: 0.8rem;
}
.p_DIC_galaxy{
    font-size: 1.3rem;
}
@media    (max-width: 800px){
   
    .div_down_DIC{
        display: flex;
        flex-direction: column;
    }
    .div_up_DIC > h3{
        font-size: 1.5rem;
    }
    .discoverme{
        grid-template-rows: 7% 93%;
    }
    .div_up_DIC_part_2 > p{
        font-size: 3vw;
    }
    .btn_DIC_top{font-size: 2.2vw;}
}
@media    (800px <= width <= 1300px){
    .card_DIC{
        width: 10vw;
    }
    .div_down_DIC{
        grid-template-columns: auto auto;
    }
    .div_down_DIC >div:nth-child(3){
        display: none;
    }
    
   
}