
*{
    color: white;
    margin: 0;
    font-family: WorkSans;

    padding: 0;
}
body{
    background-color: #2b2b2b;
/*  */
}
main{
    width: 100%;
    height: 42rem;
    display: flex;
    gap: 3rem;
    flex-direction: row;


}

ul{
    list-style: none;
}
.ul_right{
    display: flex;
    flex-direction: column;
    gap: 1rem;
    width: 50%;
    padding-top: 8rem;
}
.first_li ,.secound_li{
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
}
.secound_li > p{
    font-size: 1.3rem;
}
h3{
    font-size: 3rem;
}
.ul_left{
    width: 50%;
}
.img_left{
    width: 100%;
    height: 100%;
}
.btn_meta{
    background-color: #3B3B3B;
    border: 1px solid #A259FF;
    cursor: pointer;
    border-radius: 20px;
    justify-content: center;
    align-items: center;
    display: flex;
    flex-direction: row;
    gap: 1rem;
    width: 20rem;
    height: 5rem;

}
.btn_meta > p{
    font-size: 1.5rem;
    font-weight: bolder;
}
.img_btn{
    width: 3rem;
    height: 3rem;
}
.last_li{
    margin-top: 2rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
@media only screen and (max-width: 800px) {
    main{
        display: flex;
        flex-direction: column;
        height: auto;
        gap: 0rem;
    }
    .ul_right{
        width: 100%;
        align-items: center;
    }
    .ul_left{
        width: 100%;
        height: 14rem;
    }

}