

header{
    width: 100%;
    /* background-color: #A259FF; */
    height: 6rem;
    display: grid;
    justify-content: space-between;
    grid-template-columns: auto auto;
   
}
.li_top{
    display: flex; flex-direction: row; gap: 1rem;
}
.i_header{
    color: #A259FF;
}
.p_left_header{
    margin-left: 1rem;
    cursor: pointer;
    font-size: 2rem;
}
.p_left_header:hover{
    color: #A259FF;
}

.left_header{
    padding: 1rem;
    width: 50vw;
    display: flex;
    flex-direction: row;
  }
  .right_header{
    padding: 1rem;
      width: 40vw;
      display: grid;
      list-style: none;
      grid-template-columns: auto auto auto auto;
  
  }
  #i_hamerger{
    display: none;
}

.mobile_underProfile_img{
   display: none;
}
.btn_start_mobile{
    display: none;
}
#p_h_1,#p_h_2,#p_h_3{
    font-size: 1.3vw;
    cursor: pointer;
}
#p_h_2:hover,#p_h_3:hover,#p_h_1:hover{
    color: #A259FF;
}
.btn_right_header{
    width: 8vw;
    font-size: 1vw;
    background-color: #A259FF;
    text-align: center;
    border-radius: 15px;
    border: 0px;
    color: white;
    height: 2rem;
    cursor: pointer;

}
/* humbergermenu */
.li_first_H{
    margin-right: -3.5rem;  background-color:black;
    z-index: 1;

    display: none;
}
.div_page_H{
    width:50vw;height: auto;
}
.ul_inside_H{
    list-style: none; display: flex; flex-direction: column; align-items: flex-end ;
}
.up_li_H{
    padding: 1rem;
}
.last_li_H{
    margin: auto;
    padding-bottom: 1.5rem;
    right: 0; margin-top: 1rem; align-items: flex-start; display: flex;flex-direction: column; gap: 2rem; font-size: 1.6rem;
}
.close_list{
    cursor: pointer;
}
.last_li_H > p:hover ,.close_list:hover{
    color: #A259FF;
    cursor: pointer;

    color: #A259FF;
    
}
  @media only screen and (max-width: 800px) {
   .p_left_header{
    font-size: 1rem;
   }
    #i_hamerger{
        display: block;
    }
    #i_hamerger:hover{
    
        color: #A259FF;
        cursor: pointer;
    }
    #p_h_1,#p_h_2,#p_h_3,.btn_right_header{
        display: none;
    }
    .left_header{
        width: auto;
    }
    header{
        width: 90vw;
    }
    .right_header{
        width: auto;
        justify-content: flex-end;
    }
}