
.howitwork{
    display: grid;
    grid-template-rows: 20% 80%;
    margin-top: 5rem;
    width: 62vw;
    height: auto;
    gap: 2rem;
    margin-left: 15vw;
}
.div_up_HW > p{
    margin-top: 1rem;
     
     font-size: 1.1vw;
    
} 
.div_up_HW > h3{
    font-size: 2vw;
    
} 
.div_down_HW{
    display: grid;
    grid-template-columns: auto auto auto;
    width: 62vw;
    height: auto;
}
.card_HW{

    background-color: #3B3B3B;
    border-radius: 15px;
    width: 18vw;
    height: auto;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    align-items: center;
    justify-content: center;
    text-align: center;
}
.div_p_HW > h4{
    font-size: 1.1vw;
}
.div_p_HW > p{
    font-size: 0.85vw;
    line-height: 1.5;}
.img_HW{
    width: 14vw;

}
@media    (max-width: 800px){
    .howitwork{
        grid-template-rows: 10% 90%;
    }
    .div_p_HW > h4{
    font-size: 2.1vw;
      }
     .div_p_HW > p{
        margin-top: 0.45rem;
        margin-right: 1rem;

    font-size: 2.2vw;
    line-height: 1.7;}
    .div_up_HW > h3{
    font-size: 5.1vw;
      }
     .div_up_HW > p{
       

    font-size: 3.2vw;
    line-height: 1.7;}
    .div_down_HW{
        display: flex;
        flex-direction: column;
        gap: 1rem;
    }
    .card_HW{
        height: 10rem;
        width: auto;
    }
    .card_HW{
        display: flex;
        justify-content: center;
        flex-direction: row;
    }
    .img_HW{
        width: 22vw;
    }
    
}

