.notifEmail{
    background-color:#3B3B3B ;
    top: 0;
    right: 0;
    left: 0;
    margin: auto;
    border-radius: 15px;
    height: auto;
    width: 62vw;
    margin-top: 2rem;
    display: flex;
    flex-direction: row;
    padding: 2.2rem;
    gap: 1rem;
}
.img_NE{
    width: 35vw;
    border-radius: 15px;
}
.right_div_NE{
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
}
.right_div_NE > button {
    width: 25vw;
    cursor: pointer;
  display: flex;
    border-radius: 20px;
    background-color: #A259FF;
    color: white;
    font-size: 2vw;
    border: 0;
    text-align: center;
    align-items: center;
    height: 3rem;
    text-align: center;
    display: flex;
    justify-content: center;
}
.right_div_NE > input {
    width: 25vw;
    border-radius: 20px;
    border: 0;
    align-items: center;
    height: 3rem;
    text-align: center;
    display: flex;
    justify-content: center;
    color: black;
}
@media only screen and (max-width: 800px) {
   
    .right_div_NE > input {
        font-size: 7px;
        color: black;
    }
    .right_div_NE > button {
        font-size: 1rem;
    }
    .h3_mob{
        font-size: 1rem;
    }
    .p_pmob{
        font-size: 0.85rem;
    }
 }