
.BrowseCategories{
    margin-top: 13rem;
      width: 62vw;
      margin-left: 15vw;
     height: auto;
    display: grid;
    grid-template-rows: 30% 70%;
}
.h3_up_BC{
    font-size: 2vw;
}
.down_div_BC{
    margin-top: 2rem;
    display: grid;
    gap: 1.5rem;
    grid-template-columns: auto auto auto auto;
}
.div_catagoriy_prof{
    width: 15rem;
    height: 18rem;
    
    border-radius: 15px;
    background-color: #3B3B3B;
    display: grid;
    grid-template-rows: 80%;
}
.div_background_BC{
    align-items: center;
    justify-content: center;
    display: flex;
    text-align: center;

}
.div_background_BC > i{
    filter: blur(0rem);

     color: white;
     position: absolute;
     display: flex;

}
.icons{
    position: absolute;
}
.img_back_BC{
    width: 100%; height: 100%;filter: blur(0.4rem);
}
.div_art_BC{
    font-size: 1.5vw;
    padding: 1rem;
    font-family: WorkSans;

}
@media    (max-width: 800px){
    
    .BrowseCategories{
        margin-top: 2rem;
        
    }
    .down_div_BC{
        grid-template-columns: auto auto;
        
    }
    .div_catagoriy_prof{
        width: 30vw;
        height: 10rem;
    }
    .div_art_BC > p{
        font-size: 2.1vw;
    }
    .BrowseCategories{
        grid-template-rows: auto auto;
        margin-left: 15vw;
    }
    .h3_up_BC{
        font-size: 1.2rem;
    }

}
@media    (800px <= width <= 1300px){
    .div_catagoriy_prof{
        width: 11rem;
        height:12rem;
    }
    .BrowseCategories{
        margin-left: 15vw;
    }
    
    
   
}